___CSS_LOADER_EXPORT___.push([module.id, ".category-form[data-v-46bc1a22]{margin:0 auto;max-width:900px}.form-group label[data-v-46bc1a22]{color:#495057;font-weight:600}.invalid-feedback[data-v-46bc1a22]{display:block}.card-header h6[data-v-46bc1a22],.custom-control-label[data-v-46bc1a22]{font-weight:600}.list-unstyled li[data-v-46bc1a22]{margin-bottom:.25rem}", "",{"version":3,"sources":["webpack://./resources/js/components/forms/CategoryForm.vue"],"names":[],"mappings":"AA4SA,gCAEA,aAAA,CADA,eAEA,CAEA,mCAEA,aAAA,CADA,eAEA,CAEA,mCACA,aACA,CAMA,wEACA,eACA,CAEA,mCACA,oBACA","sourcesContent":["<template>\r\n    <div class=\"category-form\">\r\n        <div class=\"card\">\r\n            <div class=\"card-header\">\r\n                <h3 class=\"card-title\">\r\n                    <i class=\"fas fa-tags mr-2\"></i>\r\n                    {{ isEditing ? 'Editar Categoría' : 'Crear Nueva Categoría' }}\r\n                </h3>\r\n                <div class=\"card-tools\">\r\n                    <router-link :to=\"{ name: 'categories' }\" class=\"btn btn-outline-secondary btn-sm\">\r\n                        <i class=\"fas fa-arrow-left mr-1\"></i>\r\n                        Volver a Categorías\r\n                    </router-link>\r\n                </div>\r\n            </div>\r\n\r\n            <div class=\"card-body\">\r\n                <form @submit.prevent=\"submitForm\">\r\n                    <div class=\"row\">\r\n                        <div class=\"col-md-8\">\r\n                            <div class=\"form-group\">\r\n                                <label for=\"name\">Nombre de la Categoría *</label>\r\n                                <input \r\n                                    id=\"name\"\r\n                                    v-model=\"form.name\" \r\n                                    type=\"text\" \r\n                                    class=\"form-control\"\r\n                                    :class=\"{ 'is-invalid': errors.name }\"\r\n                                    required\r\n                                    placeholder=\"Ej: Cruiser 40+, Elite Men, etc.\"\r\n                                >\r\n                                <div v-if=\"errors.name\" class=\"invalid-feedback\">\r\n                                    {{ errors.name[0] }}\r\n                                </div>\r\n                            </div>\r\n\r\n                            <div class=\"form-group\">\r\n                                <label for=\"description\">Descripción</label>\r\n                                <textarea \r\n                                    id=\"description\"\r\n                                    v-model=\"form.description\"\r\n                                    class=\"form-control\"\r\n                                    rows=\"3\"\r\n                                    :class=\"{ 'is-invalid': errors.description }\"\r\n                                    placeholder=\"Describe los criterios y características de esta categoría...\"\r\n                                ></textarea>\r\n                                <div v-if=\"errors.description\" class=\"invalid-feedback\">\r\n                                    {{ errors.description[0] }}\r\n                                </div>\r\n                            </div>\r\n\r\n                            <div class=\"row\">\r\n                                <div class=\"col-md-6\">\r\n                                    <div class=\"form-group\">\r\n                                        <label for=\"min_age\">Edad Mínima</label>\r\n                                        <input \r\n                                            id=\"min_age\"\r\n                                            v-model.number=\"form.min_age\" \r\n                                            type=\"number\" \r\n                                            class=\"form-control\"\r\n                                            :class=\"{ 'is-invalid': errors.min_age }\"\r\n                                            min=\"0\"\r\n                                            max=\"100\"\r\n                                        >\r\n                                        <div v-if=\"errors.min_age\" class=\"invalid-feedback\">\r\n                                            {{ errors.min_age[0] }}\r\n                                        </div>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"col-md-6\">\r\n                                    <div class=\"form-group\">\r\n                                        <label for=\"max_age\">Edad Máxima</label>\r\n                                        <input \r\n                                            id=\"max_age\"\r\n                                            v-model.number=\"form.max_age\" \r\n                                            type=\"number\" \r\n                                            class=\"form-control\"\r\n                                            :class=\"{ 'is-invalid': errors.max_age }\"\r\n                                            min=\"0\"\r\n                                            max=\"100\"\r\n                                        >\r\n                                        <div v-if=\"errors.max_age\" class=\"invalid-feedback\">\r\n                                            {{ errors.max_age[0] }}\r\n                                        </div>\r\n                                    </div>\r\n                                </div>\r\n                            </div>\r\n\r\n                            <div class=\"form-group\">\r\n                                <label for=\"gender\">Género</label>\r\n                                <select \r\n                                    id=\"gender\"\r\n                                    v-model=\"form.gender\" \r\n                                    class=\"form-control\"\r\n                                    :class=\"{ 'is-invalid': errors.gender }\"\r\n                                >\r\n                                    <option value=\"\">Mixto</option>\r\n                                    <option value=\"male\">Masculino</option>\r\n                                    <option value=\"female\">Femenino</option>\r\n                                </select>\r\n                                <div v-if=\"errors.gender\" class=\"invalid-feedback\">\r\n                                    {{ errors.gender[0] }}\r\n                                </div>\r\n                            </div>\r\n\r\n                            <div class=\"form-group\">\r\n                                <label for=\"status\">Estado</label>\r\n                                <select \r\n                                    id=\"status\"\r\n                                    v-model=\"form.status\" \r\n                                    class=\"form-control\"\r\n                                    :class=\"{ 'is-invalid': errors.status }\"\r\n                                >\r\n                                    <option value=\"active\">Activa</option>\r\n                                    <option value=\"inactive\">Inactiva</option>\r\n                                </select>\r\n                                <div v-if=\"errors.status\" class=\"invalid-feedback\">\r\n                                    {{ errors.status[0] }}\r\n                                </div>\r\n                            </div>\r\n\r\n                            <div class=\"form-group\">\r\n                                <div class=\"custom-control custom-checkbox\">\r\n                                    <input \r\n                                        id=\"is_competitive\"\r\n                                        v-model=\"form.is_competitive\"\r\n                                        type=\"checkbox\"\r\n                                        class=\"custom-control-input\"\r\n                                    >\r\n                                    <label class=\"custom-control-label\" for=\"is_competitive\">\r\n                                        Categoría competitiva\r\n                                    </label>\r\n                                </div>\r\n                                <small class=\"form-text text-muted\">\r\n                                    Las categorías competitivas participan en campeonatos oficiales\r\n                                </small>\r\n                            </div>\r\n                        </div>\r\n\r\n                        <div class=\"col-md-4\">\r\n                            <div class=\"card bg-light\">\r\n                                <div class=\"card-header\">\r\n                                    <h6 class=\"card-title mb-0\">\r\n                                        <i class=\"fas fa-info-circle mr-1\"></i>\r\n                                        Información de Ayuda\r\n                                    </h6>\r\n                                </div>\r\n                                <div class=\"card-body\">\r\n                                    <h6 class=\"text-primary\">Ejemplos de Categorías BMX:</h6>\r\n                                    <ul class=\"list-unstyled small\">\r\n                                        <li><strong>Strider:</strong> 2-4 años</li>\r\n                                        <li><strong>Novice 5-6:</strong> 5-6 años, principiantes</li>\r\n                                        <li><strong>Intermediate 7-8:</strong> 7-8 años, nivel medio</li>\r\n                                        <li><strong>Expert 9-10:</strong> 9-10 años, avanzado</li>\r\n                                        <li><strong>Cruiser 45+:</strong> 45+ años, bicicleta cruiser</li>\r\n                                        <li><strong>Elite Women:</strong> Mujeres élite</li>\r\n                                        <li><strong>Elite Men:</strong> Hombres élite</li>\r\n                                    </ul>\r\n                                    \r\n                                    <hr>\r\n                                    \r\n                                    <h6 class=\"text-primary\">Consejos:</h6>\r\n                                    <ul class=\"list-unstyled small text-muted\">\r\n                                        <li>• Usa nombres claros y descriptivos</li>\r\n                                        <li>• Define rangos de edad apropiados</li>\r\n                                        <li>• Considera el nivel de habilidad</li>\r\n                                        <li>• Marca como competitiva si es oficial</li>\r\n                                    </ul>\r\n                                </div>\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                </form>\r\n            </div>\r\n\r\n            <div class=\"card-footer\">\r\n                <div class=\"d-flex justify-content-between\">\r\n                    <router-link :to=\"{ name: 'categories' }\" class=\"btn btn-secondary\">\r\n                        <i class=\"fas fa-times mr-1\"></i>\r\n                        Cancelar\r\n                    </router-link>\r\n                    <button \r\n                        type=\"submit\" \r\n                        @click=\"submitForm\"\r\n                        :disabled=\"loading\" \r\n                        class=\"btn btn-primary\"\r\n                    >\r\n                        <i v-if=\"loading\" class=\"fas fa-spinner fa-spin mr-1\"></i>\r\n                        <i v-else class=\"fas fa-save mr-1\"></i>\r\n                        {{ isEditing ? 'Actualizar Categoría' : 'Crear Categoría' }}\r\n                    </button>\r\n                </div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n    name: 'CategoryForm',\r\n    data() {\r\n        return {\r\n            loading: false,\r\n            form: {\r\n                name: '',\r\n                description: '',\r\n                min_age: null,\r\n                max_age: null,\r\n                gender: '',\r\n                status: 'active',\r\n                is_competitive: false\r\n            },\r\n            errors: {}\r\n        };\r\n    },\r\n    computed: {\r\n        isEditing() {\r\n            return !!this.$route.params.id;\r\n        },\r\n        categoryId() {\r\n            return this.$route.params.id;\r\n        }\r\n    },\r\n    mounted() {\r\n        if (this.isEditing) {\r\n            this.loadCategory();\r\n        }\r\n    },\r\n    methods: {\r\n        async loadCategory() {\r\n            this.loading = true;\r\n            try {\r\n                const response = await fetch(`/api/categories/${this.categoryId}`);\r\n                if (!response.ok) {\r\n                    throw new Error('Error al cargar la categoría');\r\n                }\r\n                const data = await response.json();\r\n                this.form = { ...this.form, ...data };\r\n            } catch (error) {\r\n                console.error('Error loading category:', error);\r\n                this.showNotification('Error al cargar la categoría', 'error');\r\n                this.$router.push({ name: 'categories' });\r\n            } finally {\r\n                this.loading = false;\r\n            }\r\n        },\r\n\r\n        async submitForm() {\r\n            this.loading = true;\r\n            this.errors = {};\r\n\r\n            try {\r\n                const url = this.isEditing ? `/api/categories/${this.categoryId}` : '/api/categories';\r\n                const method = this.isEditing ? 'PUT' : 'POST';\r\n\r\n                const response = await fetch(url, {\r\n                    method: method,\r\n                    headers: {\r\n                        'X-CSRF-TOKEN': document.querySelector('meta[name=\"csrf-token\"]').getAttribute('content'),\r\n                        'Content-Type': 'application/json',\r\n                        'Accept': 'application/json'\r\n                    },\r\n                    body: JSON.stringify(this.form)\r\n                });\r\n\r\n                const data = await response.json();\r\n\r\n                if (!response.ok) {\r\n                    if (data.errors) {\r\n                        this.errors = data.errors;\r\n                    }\r\n                    throw new Error(data.message || 'Error al guardar la categoría');\r\n                }\r\n\r\n                this.showNotification(\r\n                    this.isEditing ? 'Categoría actualizada exitosamente' : 'Categoría creada exitosamente',\r\n                    'success'\r\n                );\r\n                \r\n                this.$router.push({ name: 'categories' });\r\n\r\n            } catch (error) {\r\n                console.error('Error saving category:', error);\r\n                this.showNotification(error.message || 'Error al guardar la categoría', 'error');\r\n            } finally {\r\n                this.loading = false;\r\n            }\r\n        },\r\n\r\n        showNotification(message, type = 'info') {\r\n            console.log(`${type.toUpperCase()}: ${message}`);\r\n            if (type === 'error') {\r\n                alert('Error: ' + message);\r\n            }\r\n        }\r\n    }\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.category-form {\r\n    max-width: 900px;\r\n    margin: 0 auto;\r\n}\r\n\r\n.form-group label {\r\n    font-weight: 600;\r\n    color: #495057;\r\n}\r\n\r\n.invalid-feedback {\r\n    display: block;\r\n}\r\n\r\n.custom-control-label {\r\n    font-weight: 600;\r\n}\r\n\r\n.card-header h6 {\r\n    font-weight: 600;\r\n}\r\n\r\n.list-unstyled li {\r\n    margin-bottom: 0.25rem;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
